const canvasStyle = 'display:block;position:absolute;top:0;left:0;height:100%;width:100%;overflow:hidden;pointer-events:none;z-index:-1;opacity:0.5';
Array.prototype.remove = function (val) {
    var index = -1;
    for (var i = 0; i < this.length; i++) {
        if (this[i] == val) {
            index = i;
        }
    }
    if (index > -1) {
        this.splice(index, 1);
    }
};

export default class Snake {
    constructor(el = document.body) {
        this.el = el;
        this.count = 1;     // 当前snake长度
        this.size = 10;   // 方块大小
        this.snake = new Array();       // 蛇身
        this.snakeColor = "#666666";
        this.foodColor = "red";
        this.TIME = 20;    //蛇的速度
        this.count = 1;     //蛇的长度
        this.levelCount = 0;        // 当满30后减速
        this.judgeDirArray = [1, 2, 3, 4]; // 判断方向时用的数组
        this.canvas = this.newCanvas();
        this.context = this.canvas.getContext('2d');
        this.head = this.randomPoint();   // 蛇头
        this.setFood();  // 创建食物
        this.dir = Math.floor(Math.random() * 4 + 1);   //蛇的方向 1: ↓ 2:←  3:↑ 4:→
        this.lastDir = this.dir;
        this.run = true;

        this.bindEvent();
        this.drawSnake();

        this.drawCanvas();
    }

    bindEvent() {
        document.addEventListener("dblclick", () => {
            this.run = !this.run;
            if (this.run) {
                this.drawCanvas();
            }
        });
        document.addEventListener('visibilitychange', () => {
            if (document.visibilityState == 'hidden') {
                this.run = !this.run;
            } else {
                this.run = true;
                this.drawCanvas();
            }
        });
        window.onresize = () => {
            console.log("size change");
            this.canvas.width = this.el.clientWidth;
            this.canvas.height = this.el.clientHeight;
        };
    }

    requestFrame(func) {
        this.tid = requestAnimationFrame(() => func.call(this));
    }

    drawCanvas() {
        setTimeout(this.moveSnake.bind(this), this.TIME);
    }

    // 返回canvas
    newCanvas() {
        if (getComputedStyle(this.el).position === 'static') {
            this.el.style.position = 'relative';
        }
        console.log('create new canvas.');
        var bodyStyle = "width:100%;height:100%";
        document.body.style.cssText= bodyStyle;
        document.documentElement.style.cssText = bodyStyle;
        const canvas = document.createElement('canvas');
        canvas.style.cssText = canvasStyle;

        canvas.width = this.el.clientWidth;
        canvas.height = this.el.clientHeight;

        this.el.appendChild(canvas);
        return canvas;
    }

    // 生成随机点
    randomPoint() {
        var xAxis = this.size * Math.floor(Math.random() * this.canvas.width / this.size);
        var yAxis = this.size * Math.floor(Math.random() * this.canvas.height / this.size);

        return {
            x: xAxis,
            y: yAxis
        }
    }

    // 定时调用移动函数
    moveSnake() {
        const width = this.canvas.width;
        const height = this.canvas.height;
        // console.log('当前长度: ',this.count);

        // 移动蛇,判断条件
        if (this.head.x < this.food.x) {        // 是否向右
            // this.dir = this.dir != 2 ? 4 : 1;
            if (this.dir != 2) {
                this.dir = 4;
            } else {
                if (this.head.y + this.size >= height) {
                    this.dir = 3;
                } else {
                    this.dir = 1;
                }
            }
        } else if (this.head.x > this.food.x) {     // 是否向左
            // this.dir = this.dir != 4 ? 2 : 3;
            if (this.dir != 4) {
                this.dir = 2;
            } else {
                if (this.head.y + this.size >= height) {
                    this.dir = 3;
                } else {
                    this.dir = 1;
                }
            }
        } else if (this.head.x == this.food.x) {
            if (this.head.y < this.food.y) {        // 是否向下
                // this.dir = this.dir != 3 ? 1 : 2;
                if (this.dir != 3) {
                    this.dir = 1;
                } else {
                    if (this.head.x + this.size >= width) {
                        this.dir = 2;
                    } else {
                        this.dir = 4;
                    }
                }
            } else if (this.head.y > this.food.y) {   // 是否向上
                // this.dir = this.dir != 1 ? 3 : 4;
                if (this.dir != 1) {
                    this.dir = 3;
                } else {
                    if (this.head.x + this.size >= width) {
                        this.dir = 2;
                    } else {
                        this.dir = 4;
                    }
                }
            }
        }
        switch (this.dir) {
            case 1:
                this.head.y = this.head.y + this.size;
                break;
            case 2:
                this.head.x = this.head.x - this.size;
                break;
            case 3:
                this.head.y = this.head.y - this.size;
                break;
            case 4:
                this.head.x = this.head.x + this.size;
                break;
        }
        this.moveOne(this.head.x, this.head.y);
        this.setLevel();
        if (this.snake.length < (width / this.size * height / this.size) && this.run) {
            this.drawCanvas();
        }

    }

    // 判断条件 todo
    judgeDir() {
        // 移动蛇,判断条件
        var is_dead = false;
        var tempX = this.head.x, tempY = this.head.y;
        switch (this.dir) {
            case 1:
                tempY = this.head.y + this.size;
                break;
            case 2:
                tempX = this.head.x - this.size;
                break;
            case 3:
                tempY = this.head.y - this.size;
                break;
            case 4:
                tempX = this.head.x + this.size;
                break;
        }
        if (tempX > this.canvas.width || tempY > this.canvas.height || tempX < 0 || tempY < 0) {
            is_dead = true;
        }
        if (!is_dead) {
            for (var i = 1; i < this.snake.length; i++) {
                if (tempX == this.snake[i][0] && tempY == this.snake[i][1]) {
                    // if(this.snake[i][0] == this.snake[0][0] && this.snake[i][1] == this.snake[0][1]){
                    is_dead = true;
                }
            }
        }
        if (is_dead) {
            if (this.judgeDirArray.length == 0) {
                return false;
            } else {
                this.judgeDirArray.remove(this.dir);
                this.dir = this.judgeDirArray.pop();
                console.log("other dir: ", this.dir);
                this.judgeDir();
            }
        } else {
            console.log("nice dir: ", this.dir);
            this.judgeDirArray = [1, 2, 3, 4];
            this.head.x = tempX;
            this.head.y = tempY;
            return true;
        }
    }

    // 游戏开始的时候画一个蛇头..
    drawSnake() {
        this.context.fillStyle = this.snakeColor;
        // 画蛇头
        this.context.fillRect(this.head.x, this.head.y, this.size, this.size);
        this.snake.push([this.head.x, this.head.y]);
    }


    //-----------------  放食物--------------------------
    setFood() {
        do {
            this.food = this.randomPoint();
        } while (this.foodInSnake());
        this.context.fillStyle = this.foodColor;
        this.context.fillRect(this.food.x, this.food.y, this.size, this.size);
    }

    foodInSnake() {
        for (var i = 0; i < this.snake.length; i++) {
            if (this.food.x == this.snake[i][0] && this.food.y == this.snake[i][1]) {
                return true;
            }
        }
        return false;
    }

    setLevel() {
        if (this.levelCount == 40) {
            if (this.TIME < 80) {
                this.TIME += 10;
            }
            this.levelCount = 0;
        }
    }

    //-------------- 移动一格
    moveOne(x, y) {
        this.context.fillStyle = this.snakeColor;
        var w = this.size, h = this.size;
        var lastX, lastY;
        if (this.lastDir != this.dir) {
            if ((this.lastDir == 1 && this.dir == 2) || (this.lastDir == 3 && this.dir == 2)) {
                lastX = x + this.size;
                lastY = y;
            } else if ((this.lastDir == 2 && this.dir == 3) || (this.lastDir == 4 && this.dir == 3)) {
                lastX = x;
                lastY = y + this.size;
            } else if ((this.lastDir == 3 && this.dir == 4) || (this.lastDir == 1 && this.dir == 4)) {
                lastX = x - this.size;
                lastY = y;
            } else if ((this.lastDir == 2 && this.dir == 1) || (this.lastDir == 4 && this.dir == 1)) {
                lastX = x;
                lastY = y - this.size;
            }
            // if((this.lastDir==3 && this.dir == 4) || (this.lastDir==2 && this.dir==1)){
            //     if(this.lastDir==3){
            //         lastX = x - this.size;
            //         lastY = y;
            //     }else if(this.lastDir==2){
            //         lastX = x;
            //         lastY = y - this.size;
            //     }
            // }else if((this.lastDir == 2 && this.dir == 3) || (this.lastDir == 1 && this.dir == 4)){
            //     if(this.lastDir == 2){
            //         lastX = x;
            //         lastY = y + this.size;
            //     }else if(this.lastDir == 1){
            //         lastX = x - this.size;
            //         lastY = y;
            //     }
            // }else if((this.lastDir == 1 && this.dir == 2) || (this.lastDir == 4 && this.dir == 3)){
            //     if(this.lastDir == 1){
            //         lastX = x + this.size;
            //         lastY = y;
            //     }else if(this.lastDir == 4){
            //         lastX = x;
            //         lastY = y + this.size;
            //     }
            //
            // }else if((this.lastDir == 4 && this.dir == 1) || (this.lastDir == 3 && this.dir == 2)){
            //     if(this.lastDir == 4){
            //         lastX = x;
            //         lastY = y-this.size;
            //     }else if(this.lastDir == 3){
            //         lastX = x + this.size;
            //         lastY = y;
            //     }
            // }
            this.context.fillRect(lastX, lastY, this.size, this.size);
        } else {
            if (this.dir == 1 || this.dir == 3) {
                h -= 1;
            } else {
                w -= 1;
            }
        }
        this.context.fillRect(x, y, w, h);
        // 将新移动的蛇头添加到snake数组
        var newHead = [[x, y]];
        this.snake = newHead.concat(this.snake);

        if (false == this.eatFood()) {
            var tail = this.snake.pop();
            var need_clear = true;
            for (var i = 0; i < this.snake.length; i++) {
                if (tail[0] == this.snake[i][0] && tail[1] == this.snake[i][1]) {
                    need_clear = false;
                }
            }
            if (need_clear) {
                this.context.clearRect(tail[0], tail[1], this.size, this.size);
            }
        }
        this.lastDir = this.dir;
    }

    // 判断是否吃到食物
    eatFood() {
        if (this.head.x == this.food.x && this.head.y == this.food.y) {
            this.context.fillStyle = this.snakeColor;
            this.context.fillRect(this.food.x, this.food.y, this.size, this.size);
            this.setFood();
            this.count += 1;
            this.levelCount += 1;
            return true;
        }
        return false;
    }
    //
    destroy() {
        console.log("i am dead...=_=");
        window.clearTimeout();
        this.canvas.parentNode.removeChild(this.canvas);
    }

}




