<template>
  <div class="accordion" id="accordionExample">
    <div class="card" v-for="item in history">
      <div class="card-header" id="headingOne">
          <button class="btn" type="button" data-toggle="collapse" :data-target="'#'+item.id"
                  aria-expanded="false" :aria-controls="item.id" style="background-color:transparent;">
            <h5 class="mb-0">
            {{item.key}}日の任务池
            </h5>
          </button>
      </div>
      <div v-bind:id="item.id" class="collapse" aria-labelledby="headingOne" data-parent="#accordionExample">
        <div class="card-body row">
          <fieldset class="t-card col-sm-4 ">
            <legend class="t-legend t-text-gray">待办</legend>
            <ul class="list-group">
              <li class="list-group-item" v-for="value in item.todo">{{value}}</li>
            </ul>
          </fieldset>
          <fieldset class="t-card col-sm-4">
            <legend class="t-legend t-text-gray">正在做</legend>
            <ul class="list-group ">
              <li class="list-group-item" v-for="value in item.doing">{{value}}</li>
            </ul>
          </fieldset>
          <fieldset class="t-card col-sm-4">
            <legend class="t-legend t-text-gray">已完成</legend>
            <ul class="list-group ">
              <li class="list-group-item" v-for="value in item.ok">{{value}}</li>
            </ul>
          </fieldset>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

  import "./../assets/css/base.css"

  export default {
    name: "history",
    data() {
      return {
        history: [],
        save_days:5,
      }
    },
    mounted: function () {
      this.parseHistory();
    },
    methods: {
      parseOneDay(key, id) {
        let todo = window.localStorage.getItem(key + "-todo");
        let doing = window.localStorage.getItem(key + "-doing");
        let ok = window.localStorage.getItem(key + "-ok");
        // key = key.replace(".","-");
        if (!todo && !doing && !ok) {
          return;
        }
        let temp = {};
        temp.key = key.replace(".", "月");
        temp.id = id;
        if (todo) {
          // temp["todo"] = todo.split(",");
          temp["todo"] = JSON.parse(todo);
        }
        if (doing) {
          // temp["doing"] = doing.split(",");
          temp['doing'] = JSON.parse(doing);
        }
        if (ok) {
          // temp["ok"] = ok.split(",");
          temp['ok'] = JSON.parse(ok);
        }
        this.history.push(temp);
      },
      parseHistory() {
        let now = this.dayjs().format("MM.DD");
        let t_arr = [now];
        for(var i = 1;i<this.save_days;i++){
            let his_day = this.dayjs().subtract(i,'day').format("MM.DD");
            this.parseOneDay(his_day,"t"+i);
            t_arr.push(his_day);
        }
        this.clearMoreDay(t_arr);
      },
      clearMoreDay(arr) {
        let temp;
        let prefix;
        for (var i = 0; i < window.localStorage.length; i++) {
          temp = window.localStorage.key(i);
          prefix = temp.split("-")[0];
          if (arr.indexOf(prefix) < 0) {
            window.localStorage.removeItem(temp);
          }
        }
      },
      fakeData() {
        let d = ['吃饭', '睡觉', '看电视', '打酱油'];
        let his_1 = this.dayjs().subtract(1, 'day').format("MM.DD");
        let his_2 = this.dayjs().subtract(2, 'day').format("MM.DD");
        let his_3 = this.dayjs().subtract(3, 'day').format("MM.DD");
        let his_4 = this.dayjs().subtract(4, 'day').format("MM.DD");
        window.localStorage.setItem(his_1 + "-todo", d);
        window.localStorage.setItem(his_1 + "-doing", d);
        window.localStorage.setItem(his_1 + "-ok", d);
        window.localStorage.setItem(his_2 + "-todo", d);
        window.localStorage.setItem(his_2 + "-doing", d);
        window.localStorage.setItem(his_2 + "-ok", d);
        window.localStorage.setItem(his_3 + "-todo", d);
        window.localStorage.setItem(his_3 + "-doing", d);
        window.localStorage.setItem(his_3 + "-ok", d);
        window.localStorage.setItem(his_4 + "-todo", d);
        window.localStorage.setItem(his_4 + "-ok", d);
      },
    },
  }
</script>

<style scoped>
  .accordion {
    margin-top: 20px;
  }
</style>



